import { IconButton, makeStyles, withStyles } from '@material-ui/core';
import { isMobile } from 'react-device-detect';
import BookmarksIcon from '@material-ui/icons/Bookmarks';
import ShareIcon from '@material-ui/icons/Share';
import RoomIcon from '@material-ui/icons/Room';
import DashboardIcon from '@material-ui/icons/Dashboard';
import SimpleMap from './map'
import BottomNavigation from '@material-ui/core/BottomNavigation';
import BottomNavigationAction from '@material-ui/core/BottomNavigationAction';
import Drawer from '@material-ui/core/Drawer';
import { forwardRef, useState } from 'react';
import Slide from '@material-ui/core/Slide';
import Dialog from '@material-ui/core/Dialog';
import Toolbar from '@material-ui/core/Toolbar';
import CloseIcon from '@material-ui/icons/Close';
import ListItemText from '@material-ui/core/ListItemText';
import AppBar from '@material-ui/core/AppBar';
import ListItem from '@material-ui/core/ListItem';
import List from '@material-ui/core/List';

import Divider from '@material-ui/core/Divider';
import Typography from '@material-ui/core/Typography';

const NavigationBar = (props) => {
	const classes = useStyles();
	const [value, setValue] = useState(null);
	const [mapHandler, setMapHandler] = useState(false);
	const [open, setOpen] = useState(false);
	const [socialHandler, setSocialHandler] = useState(false);

	return (
		<>
			<BottomNavigation
				value={value}
				onChange={(event, newValue) => {
					setValue(newValue);
				}}
				showLabels
				className={classes.paperMain}
			>
				<BottomNavigationAction
					icon={<ShareIcon />}
					onClick={() => setSocialHandler(true)}
				/>
				<BottomNavigationAction
					icon={<RoomIcon />}
					onClick={() => setMapHandler(true)}
				/>
				<BottomNavigationAction onClick={() => setOpen(true)} icon={<DashboardIcon />} />
				<BottomNavigationAction icon={<BookmarksIcon />} />
			</BottomNavigation>
			<Location
				state={mapHandler}
				handlerClose={() => {
					setMapHandler(false);
					setValue(null);
				}}
			/>
			<FullModal
				open={open}
				handleClose={() => {
					setOpen(false);
					setValue(null);
				}}
			/>
			<SocialBar openHandler={socialHandler} closeHandler={()=> setSocialHandler(false)} />
		</>
	);
};

const Location = ({ state, handlerClose }) => {
	return (
		<Drawer anchor={'top'} open={state} onClose={handlerClose} >
			<div style={{height: 500}}>
		<SimpleMap />
			</div>
			
		</Drawer>
	);
};

const FullModal = ({ open, handleClose }) => {
	const classes = useStyles();

	return (
		<div>
			<Dialog
				fullScreen
				open={open}
				onClose={handleClose}
				TransitionComponent={Transition}
			>
				<AppBar className={classes.appBar}>
					<Toolbar>
						<IconButton
							edge='start'
							color='inherit'
							onClick={handleClose}
							aria-label='close'
						>
							<CloseIcon />
						</IconButton>
						<Typography variant='h6' className={classes.title}>
							Ejes De Campaña
						</Typography>
					</Toolbar>
				</AppBar>
				<List>
					<ListItem button>
						<ListItemText primary='Phone ringtone' secondary='Titania' />
					</ListItem>
					<Divider />
					<ListItem button>
						<ListItemText
							primary='Proyecto uno'
							secondary='descripcion proyecto uno'
						/>
					</ListItem>
				</List>
			</Dialog>
		</div>
	);
};



const SocialBar = ({openHandler, closeHandler}) => {



	return <Drawer  anchor="left" open={openHandler} onClose={closeHandler} style={{backgroundColor: 'transparent'}} >
	<div>	HOLAAA</div>
	</Drawer>
}

const Transition = forwardRef(function Transition(props, ref) {
	return <Slide direction='up' ref={ref} {...props} />;
});

const useStyles = makeStyles(() => ({
	circularBtn: {
		borderWidth: 2,
		borderColor: 'blue',
	},
	paperMain: {
		/* 	display: 'flex',
		flexWrap: 'wrap', */
		/* alignContent: 'center',
		alignItems: 'center', */
		marginRight: 'auto',
		marginLeft: 'auto',
		/* 	justifyContent: 'space-between',
		borderRadius: 30, */

		backgroundColor: 'rgba(110, 127, 141,0.7)', //'rgba(110, 127, 141,0.7)',
		width: isMobile ? 300 : 420,
		borderRadius: 15,
	},
	appBar: {
		position: 'relative',
	},
}));

export default NavigationBar;
